/* ================================
   ПРОСТОЙ + КРАСИВЫЙ CSS 2026
   ================================ */

/* ОСНОВА */
* { margin: 0; padding: 0; box-sizing: border-box; }

body {
    font-family: system-ui, -apple-system, sans-serif;
    background: linear-gradient(135deg, #1e3c72 0%, #2a5298 100%);
    min-height: 100vh;
    padding: 1rem;
}

/* КАРТОЧКИ */
.card {
    background: white;
    border-radius: 16px;
    box-shadow: 0 8px 32px rgba(0,0,0,0.1);
    overflow: hidden;
}

/* ШАПКА */
.header-gradient {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    padding: 1.5rem;
}

/* КНОПКИ */
.btn {
    border: none;
    border-radius: 8px;
    padding: 0.75rem 1.5rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s;
}

.btn-primary {
    background: linear-gradient(135deg, #4f46e5 0%, #7c3aed 100%);
    color: white;
}

.btn-primary:hover { transform: translateY(-1px); box-shadow: 0 4px 12px rgba(79,70,229,0.4); }

.btn-success {
    background: linear-gradient(135deg, #10b981 0%, #059669 100%);
    color: white;
}

.btn-success:hover { transform: translateY(-1px); box-shadow: 0 4px 12px rgba(16,185,129,0.4); }

.btn-secondary {
    background: #f3f4f6;
    color: #374151;
    border: 1px solid #d1d5db;
}

.btn-secondary:hover { background: #e5e7eb; }

/* ФОРМЫ */
.input-field {
    width: 100%;
    padding: 0.75rem;
    border: 2px solid #e5e7eb;
    border-radius: 8px;
    font-size: 1rem;
    transition: border-color 0.2s;
}

.input-field:focus {
    outline: none;
    border-color: #4f46e5;
    box-shadow: 0 0 0 3px rgba(79,70,229,0.1);
}

/* ТАБЛИЦА МЕТРИК */
.metrics-table {
    width: 100%;
    border-collapse: collapse;
    background: white;
    border-radius: 12px;
    overflow: hidden;
    box-shadow: 0 4px 16px rgba(0,0,0,0.08);
}

.metrics-table th {
    background: linear-gradient(135deg, #f8fafc 0%, #f1f5f9 100%);
    color: #1e293b;
    padding: 1rem;
    text-align: left;
    font-weight: 600;
    font-size: 0.875rem;
    text-transform: uppercase;
    letter-spacing: 0.05em;
}

.metrics-table td {
    padding: 0.875rem 1rem;
    border-bottom: 1px solid #f1f5f9;
}

/* ВРЕМЯ — ОДИНАКОВЫЙ СТИЛЬ! */
.metrics-table th:nth-child(5),
.metrics-table th:nth-child(6),
.metrics-table td:nth-child(5),
.metrics-table td:nth-child(6) {
    font-family: 'SF Mono', Monaco, 'Courier New', monospace;
    text-align: right;
    color: #059669;
    font-weight: 600;
    font-size: 0.875rem;
}

.metrics-table tr:hover {
    background: linear-gradient(90deg, #f8fafc 0%, #f0f9ff 100%);
}

/* СТАТУСЫ */
.hit { color: #10b981; font-weight: 600; }
.miss { color: #ef4444; font-weight: 600; }

/* ЛЕЙАУТ */
.page-container {
    max-width: 1200px;
    margin: 0 auto;
}

.flex { display: flex; gap: 1.5rem; }
.flex-col { flex-direction: column; }

@media (max-width: 768px) {
    .flex { flex-direction: column; }
    body { padding: 0.5rem; }
}
